import fetch from '@/utils/fetch'

//搜索所有
export function receiptAccount(query) {
    return fetch({
        url: 'admin/receiptaccount/query',
        method: 'post',
        data: query
    })
}

//启用单独一个账户
export function receiptaccountNum(query) {
    return fetch({
        url: 'admin/receiptaccount/' + query,
        method: 'put',
    })
}

/*
//禁用单独一个账户
export function forbid(query) {
    return fetch({
        url: '/admin/receiptaccount/' + query ,
        method: 'post',
    })
}*/
export function addAccount(query) {
    return fetch({
        url: 'admin/receiptaccount',
        method: 'POST',
        data: query
    })
}

export function currencytrans(query) {
    return fetch({
        url: 'admin/currencytrans',
        method: 'GET',
        params: query
    })
}

export function educeExcel(query) {
    return fetch({
        url: 'admin/currencytrans/nofilter/export',
        method: 'GET',
        params: query
    })
}

/**
 * 这是现金钱包列表
 * @param query
 */
export function entitywallet(query) {
    return fetch({
        url: 'admin/entitywallet',
        method: 'GET',
        params: query
    })
}

/**
 * 这是虚拟币钱包列表
 * @param query
 */

export function currencywallet(query) {
    return fetch({
        url: 'admin/currencywallet',
        method: 'GET',
        params: query
    })
}

export function entrustrans(query) {
    return fetch({
        url: 'admin/entrustrans',
        method: 'GET',
        params: query
    })
}

export function entrustdetails(query) {
    return fetch({
        url: 'admin/entrustdetails',
        method: 'GET',
        params: query
    })
}

export function coinlist() {
    return fetch({
        url: 'admin/trademappings',
        method: 'GET'
    })
}

export function exportData(params) {
    return fetch({
        url: '/admin/exportCurrencywallet',
        method: 'GET',
        params
    })
}



