<template>
    <div class="p-20">
        <div style="margin: 20px 0">
            <el-button type="primary" @click="addAubmission">新增</el-button>
            <el-select clearable style="width: 120px;margin-left: 300px" class="filter-item"
                       v-model="listQuery.bankStatus">
                <el-option v-for="status in bankStatusArray" :key="status.display_name" :label="status.display_name"
                           :value="status.key">
                </el-option>
            </el-select>

            <el-button class="filter-item" @click="handleFilter" type="primary" size="small" style="margin-left: 30px">查询</el-button>
        </div>
        <div>
            <el-table
                    ref="singleTable"
                    :data="tableData"
                    v-loading="listLoading"
                    element-loading-text="给我一点时间"
                    highlight-current-row
                    @current-change="handleCurrentChange"
                    style="width: 100%;">
                <el-table-column
                        type="index"
                        label="序号"
                        width="90">
                </el-table-column>
                <el-table-column
                        v-for="item in tableHeader"
                        :key="item.prop"
                        :label="item.label"
                        :prop="item.prop"
                        :width="item.width"
                        :min-width="item.minWidth"
                        :align="item.align"
                        :formatter="item.formatter"
                        show-overflow-tooltip>
                </el-table-column>
                <el-table-column
                        fixed="right"
                        label="操作"
                        width="85">
                    <template scope="scope" class="filter-operation">
                        <ul style="list-style: none;padding: 0px;margin: 0px">

                            <li style="float:left;width: 30px;" v-if="scope.row.bankStatus===2">
                                <el-button
                                        type="text"
                                        @click="enableDisableAccount(1,scope.row.id,'启用')">启用
                                </el-button>
                            </li>
                            <li style="float:left;width: 30px;"
                                v-else-if="scope.row.bankStatus===1">
                                <el-button
                                        type="text"
                                        @click="enableDisableAccount(2,scope.row.id,'禁用')">禁用
                                </el-button>
                            </li>
                        </ul>
                    </template>
                </el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination @size-change="userPageSize" @current-change="userPage"
                               :current-page="listQuery.page.pageNum"
                               :page-sizes="[10,20,30, 50]" :page-size="listQuery.page.pageSize"
                               layout="total, sizes, prev, pager, next, jumper" :total="total">
                </el-pagination>
            </div>

        </div>
        <el-dialog title="新增收款账户" :visible.sync="dialogFormVisible">
            <el-form :model="ReceiptAccountVo">
                <el-form-item label="账户类型" :label-width="formLabelWidth">
                    <el-select class="filter-item" v-model="ReceiptAccountVo.bankType" placeholder="请选择类型">
                        <el-option label="网银" :value="1"></el-option>
                        <el-option label="支付宝" :value="2"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="银行名称" :label-width="formLabelWidth">
                    <el-select class="filter-item" v-model="ReceiptAccountVo.bankName" placeholder="请选择银行">
                        <el-option label="中国工商银行" :value="'中国工商银行'"></el-option>
                        <el-option label="招商银行" :value="'招商银行'"></el-option>
                        <el-option label="平安银行" :value="'平安银行'"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="开户行" :label-width="formLabelWidth" placeholder="请输入开户行地址">
                    <el-input v-model="ReceiptAccountVo.bankAddress" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="开户名称" :label-width="formLabelWidth" placeholder="请输入开户人(公司)名称">
                    <el-input v-model="ReceiptAccountVo.accountName"></el-input>
                </el-form-item>
                <el-form-item label="银行卡号" :label-width="formLabelWidth" placeholder="请输入银行卡号">
                    <el-input v-model="ReceiptAccountVo.bankAccount" auto-complete="off"
                              value="form.checkRemark"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false">取  消</el-button>
                <el-button type="primary" @click="increaseAubmission">确  定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import {receiptAccount, receiptaccountNum, addAccount} from '@/api/capital';
    import DCTP from '@/utils/dctp';

    const bankStatusArray = [
        {key: '0', display_name: '全部状态'},
        {key: 1, display_name: '启用'},
        {key: 2, display_name: '禁用'},
    ]
    export default {
        data() {
            return {
                //总条数
                total: 0,
                //返回来列表的数据
                tableData: [],
                //查看身份新增框自动隐藏
                dialogFormVisible: false,
                //查看身份信息双向绑定数据
                bankStatusArray,
                ReceiptAccountVo: {
                    //类型--账户类型
                    bankType: '',
                    //收款账户--银行名称
                    bankName: '',
                    //收款账号--银行卡号
                    bankAccount: '',
                    //开户人--开户名称
                    accountName: '',
                    //开户行--开户行
                    bankAddress: '',
                },
                strNum: '',
                //查看身份证信息的左边宽度
                formLabelWidth: '120px',
                //点击获取选择的值
                currentRow:
                    null,
                //查询条件对象
                listQuery: {
                    bankStatus: '0',
                    page: {
                        pageNum: 1,
                        pageSize: 10
                    }
                }
                ,
                msg: '',
                listLoading: true,
                tableHeader: [{
                    label: "UID",
                    prop: "id",
                    width: 80,
                }, {
                    label: "类型",
                    prop: "bankType",
                    width: 80,
                    align: 'center',
                    formatter(row, column, value) {
                        return value === 1 ? '网银' : (value === 2 ? '支付宝' : '未知');
                    }
                }, {
                    label: "收款账户",
                    prop: "bankName",
                    width: 150
                }, {
                    label: "收款账号",
                    prop: "bankAccount",
                    width: 300
                }, {
                    label: "状态",
                    prop: "bankStatus",
                    width: 110,
                    align: 'center',
                    formatter(row, column, value) {
                        let item = DCTP.utils.findDataByValue(bankStatusArray, value, 'key');
                        return item ? item.display_name : '';
                    }
                }, {
                    label: "开户人",
                    prop: "accountName",
                    width: 300
                }, {
                    label: "开户行",
                    prop: "bankAddress",
                    width: 300
                }, {
                    label: "创建时间",
                    prop: "createTime",
                    width: 170,
                    formatter(row, column, value) {
                        return DCTP.utils.parseDateTimeStr(value)
                    }
                }, {
                    label: "最后更新时间",
                    prop: "updateTime",
                    width: 170,
                    formatter(row, column, value) {
                        return DCTP.utils.parseDateTimeStr(value)
                    }
                }/* */]
            }
        },
        computed: {
            tableDataLength() {
                return this.tableData.length
            }
        }
        ,
        methods: {
            getList() {
                this.listLoading = true;
                receiptAccount(this.listQuery).then(response => {
                    this.tableData = response.data.data;
                    this.total = response.data.page.count;
                    this.listLoading = false;
                })
            },
            /**
             * 条件筛选查询对象
             * @returns {Promise}
             */
            handleFilter() {
                this.getList();
            },
            //获取当前页数
            userPage(val) {
                this.listQuery.page.pageNum = val;
                this.getList();
            },
            //获取每页多少条
            userPageSize(val) {
                this.listQuery.page.pageSize = val;
                this.getList();
            },
            /**
             *获取选中会员的所有信息
             * @returns {Promise}
             */
            addAubmission() {
                this.ReceiptAccountVo.bankType = '';
                this.ReceiptAccountVo.bankName = '';
                this.ReceiptAccountVo.bankAccount = '';
                this.ReceiptAccountVo.accountName = '';
                this.ReceiptAccountVo.bankAddress = '';
                this.dialogFormVisible = true;
            },
            increaseAccount() {
                let _this = this;
                addAccount(this.ReceiptAccountVo).then(response => {
                    if (response.result) {
                        this.msg = "操作成功!";
                        this.$message({
                            type: 'success',
                            message: this.msg
                        });
                        _this.dialogFormVisible = false;
                    } else {
                        this.msg = response.msg;
                        this.$message({
                            type: 'error',
                            message: this.msg
                        });

                    }
                    this.getList();
                })
            },
            /**
             *修改身份信息方法
             * @returns {Promise}
             */
            increaseAubmission() {
                this.increaseAccount();
            },
            receiptaccount() {
                receiptaccountNum(this.strNum).then(response => {
                    if (response.result) {
                        this.dialogFormVisible = false;
                        this.msg = "操作成功!";
                        this.$message({
                            type: 'success',
                            message: this.msg
                        });
                        this.getList();
                    } else {
                        this.msg = response.msg;
                        this.$message({
                            type: 'error',
                            message: this.msg
                        });
                        this.getList();
                    }

                });
            },
            //启用禁用
            enableDisableAccount(int, id, str) {

                if (int === 1) {
                    this.$confirm('是否确定' + str + '此用户账号', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then(() => {
                        this.strNum = id + '/enable';
                        this.receiptaccount();
                    });
                } else if (int === 2) {
                    this.$confirm('是否确定' + str + '此用户账号', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then(() => {
                        this.strNum = id + '/forbid';
                        this.receiptaccount();
                    });
                }
            }
            ,
            //点击事件获取选中的单条信息的对象值
            handleCurrentChange(val) {
                this.currentRow = val;
            }
        }
        ,
        created() {
            this.getList();
        }
    }
</script>


